@use '../../style/variables' as v;
@use '../../style/mixins' as m;

.banner {
    @include m.background-and-border();
    @include m.flex-center();
    height: 15vh;
    margin: 1.5rem 2rem;
    position: relative;
    padding: 1rem;
    &::before {
        content: '';
        position: absolute;
        border-radius: 10px;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.25); 
        z-index: 1;
    }
    h1 {
        @include m.typography(2.4rem, 700, v.$text-light);
        z-index: 2;
        position: relative;
        width: 75%;
        text-shadow: 
            -1px -1px 0 #000,  
             1px -1px 0 #000,
            -1px  1px 0 #000,
             1px  1px 0 #000;
    }
    @media (min-width: v.$md-breakpoint) {
        height: 25vh;
        justify-content: center;
        h1{
            width: 100%;
            text-align: center;
            font-size: 3rem;
        }
      }
    @media (min-width: v.$lg-breakpoint) {
            h1{
                font-size: 4.8rem;
            }
        }
}
